import React from 'react'
// 输入
class Input extends React.Component {
    render() {
        return (
            <div>
                <input
                value={this.props.inpValue}
                type="text"
                onChange = {this.props.handleChange}
                />
                <button onClick={this.props.addTodo}>添加</button>
            </div>
        )
    }
}

export default Input

