import React from 'react';
import Input from './Input';
import List from './List';
class TodoList extends React.Component {
    constructor(){
        super();
        this.state = {
            inpValue:'',
            todos:[]
        }
    }
    // 改变当前状态值，当前事件对象的值，输入触发
    handleChange = (e) =>{
        this.setState({inpValue:e.target.value})
    }
    // 挂载后立即调用，读取本地数据
    componentDidMount(){
		let todos = localStorage.getItem('todos');
		if(todos){
			this.setState({
				todos:JSON.parse(todos)
			})
		}
    }

    // 数据更新后更新本地存储
	componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }
    // 给状态对象的数组添加元素，点击按钮触发
    addTodo=()=>{
        if(this.state.inpValue===''){
            alert('输入不能为空');
            return;
        }else{
            this.setState({
                todos:[
                    {title:this.state.inpValue,done:false},
                    ...this.state.todos
                ] 
            })
            this.setState({inpValue:''});
        }
    }

    // 删除元素
    delTodo=(idx)=>{
        this.setState({
            todos:this.state.todos.filter((item,index)=>index!=idx)
		})
    }
	toggle = (index) =>{
		let newTodos = JSON.parse(JSON.stringify(this.state.todos));
		newTodos[index].done = !newTodos[index].done;
		this.setState({
			todos:newTodos
		})
    }

    // 列出数组元素
	renderTodos = (done) =>{
		return this.state.todos.map((todo,idx)=>{
			if(done==todo.done){
				return <li key={idx}>
					<input onClick={()=>this.toggle(idx)} checked={done} type='checkbox'/>
                    <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
					<button onClick={()=>this.delTodo(idx)}>删除</button>
				</li>
			}
		})
	}
    render() {
        const{todos,inpValue} = this.state;
        return (
            <div>
                <label htmlFor="inp">todolist</label>
                <Input inpValue={inpValue}
                    handleChange={this.handleChange}
                    addTodo={this.addTodo}/>
                <List renderTodos = {this.renderTodos}
                todos = {todos}/>
            </div>
        );
    }
}



export default TodoList


